import axios from "axios";

export const signInRequest = async payload =>
  axios({
    method: "post",
    url: "/User/Login",
    data: payload
  });

export const signUpRequest = async payload =>
  axios({
    method: "post",
    url: "/User/Register",
    data: payload
  });

