<template>
<div>
  <filter-toolbar :class="{'hidden-xs-only': !isFilterToolbarDisplayed}"/>
      <v-layout class="full-height" row wrap>
          <!-- <mobile-switch-button :isListDisplayed="isListDisplayed" :switchView="switchView"/> -->
        <v-flex md7 sm12 :class="{'hidden-sm-and-down': !isListDisplayed, 'hidden-drawer': !isListDisplayed}" style="position: relative">
          <house-list/>
          <desktop-drawer-button :isListDisplayed="isListDisplayed" :switchView="switchView"/>
        </v-flex>
        <v-flex md5 sm12 :class="{'hidden-sm-and-down': isListDisplayed, 'map-expanded': !isListDisplayed}">
          <house-map/>
        </v-flex>
      </v-layout>
      <bottom-toolbox class="hidden-sm-and-up" :isListDisplayed="isListDisplayed" :switchView="switchView" :switchFilters="switchFilters"/>
</div>
</template>

<script>
import HouseList from "./scenes/List/List.vue";
import HouseMap from "./scenes/Map/Map.vue";
import FilterToolbar from "./scenes/FilterToolbar/FilterToolbar.vue";
import MobileSwitchButton from "./components/MobileSwitchButton.vue";
import DesktopDrawerButton from "./components/DesktopDrawerButton.vue";
import BottomToolbox from "./components/BottomToolbox.vue";

export default {
  name: "landing",
  data: function() {
    return {
      isListDisplayed: true,
      isFilterToolbarDisplayed: false
    };
  },
  methods: {
    switchView: function() {
      this.isListDisplayed = !this.isListDisplayed;
    },
    switchFilters: function() {
      this.isFilterToolbarDisplayed = !this.isFilterToolbarDisplayed;
    }
  },
  components: {
    HouseList,
    HouseMap,
    FilterToolbar,
    MobileSwitchButton,
    DesktopDrawerButton,
    BottomToolbox
  }
};
</script>

<style lang="sass" scoped>
  .full-height
    min-height: 100%
  .hidden-drawer
    max-width: 0%
  .map-expanded
    min-width: 100%
</style>
